import React , { useState , useEffect } from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import stl from '../STYLES/FIND_JOB/findjob.module.scss'
import  States from '../components/SELECT_OPTIONS/state' 
import Nationality from '../components/SELECT_OPTIONS/nationality'
import { useQuery } from '@apollo/react-hooks';
import { GET_ALL_EMPLOYEES } from '../queries/index'
import { navigate } from 'gatsby'
import StarRatings from 'react-star-ratings';



const roundScore = e => {
  const Score = e.reduce((a,b) => a + b , 0) / e.length
  return Number(Score.toFixed(1))
  }


  const initialState = {
    employees:[],
    filters:{
      state:null,
      nationality:null,
      age:null,
      rating:null
    }
  }


const FindEmployeeBody = () => {

  const { data } = useQuery(GET_ALL_EMPLOYEES);
  const [state , setState] = useState(initialState)

  useEffect(() => {
      let filteredEmployees = data ? data.getAllEmployees : []
      filteredEmployees =  state.filters.state ? filteredEmployees.filter(x => x.location.state === state.filters.state) : filteredEmployees
      filteredEmployees =  state.filters.nationality ? filteredEmployees.filter(x => x.characteristic.nationality === state.filters.nationality) : filteredEmployees
      filteredEmployees = state.filters.age && Number(state.filters.age) === 29 ? filteredEmployees.filter(x => x.age <= 29) : filteredEmployees
      filteredEmployees = state.filters.age && Number(state.filters.age) === 39 ? filteredEmployees.filter(x => 30 <= x.age && x.age <= 39) : filteredEmployees
      filteredEmployees = state.filters.age && Number(state.filters.age) === 40 ? filteredEmployees.filter(x => x.age >= 40) : filteredEmployees
      filteredEmployees =  state.filters.rating === 'high' ? filteredEmployees.sort((a,b) => roundScore(b.score) - roundScore(a.score)) : filteredEmployees.sort((a,b) => roundScore(b.score) - roundScore(a.score))
      filteredEmployees =  state.filters.rating === 'low' ? filteredEmployees.sort((a,b) => roundScore(a.score) - roundScore(b.score)) : filteredEmployees.sort((a,b) => roundScore(b.score) - roundScore(a.score))
      setState({...state, employees:filteredEmployees})

  },[data , state.filters])

  const filetJobs = e => {
    const { name , value } = e.target
    const filters = {
    ...state.filters,
    [name]:value
    }
    setState({...state , filters})
  }

console.log(state)
  return(
    <div className = {stl.background}>
    <h1 className = {stl.h1}>FIND AN EMPLOYEE</h1>
    <div className = {stl.container}>
    <div className = {stl.filter}>
       <h5>FILTER</h5>
      <div className = {stl.selectors}>
          <States name = "state" onChange = {e => filetJobs(e)} />
          <Nationality name = 'nationality' onChange = {e => filetJobs(e)} />
          <select name='age'  onChange = {e => filetJobs(e)}>
                 <option hidden value={null}>Age</option>
                 <option value="">Age</option>
                 <option value={29}>18-29</option>
                 <option value={39}>30-39</option>
                 <option value={40}>40+</option>
          </select>
          <select name='rating'  onChange = {e => filetJobs(e)}>
                 <option hidden value={null}>Rating</option>
                 <option value="">Rating</option>
                 <option value='high'>high</option>
                 <option value='low'>low</option>
          </select>
      </div>
   </div>
   <ul className = {stl.employeesUl}>
        {state.employees.length ? state.employees.map(({_id , score , username , location ,  name , characteristic , age }) => (
          <li key = {_id}>
             <h4 className = {stl.employeeName}>{name}</h4>
               <p><strong><u>nationality:</u></strong> {characteristic.nationality ? characteristic.nationality : "-"}</p>
               <p><strong><u>age:</u></strong> {age ? age : "-"}</p>
               <p><strong><u>Languages:</u></strong></p>
               <p>{characteristic.languages.length ? characteristic.languages.map(x => x + ' ') : "-"}</p>
             <h4>Location</h4>
              <p><u>state:</u> {location.state ? location.state : "-"}</p>
              <p><u>city:</u> {location.city ? location.city : "-"}</p>
              <h4>{name + '`s'} Rating:</h4>
              <StarRatings
                    rating={score.length ? roundScore(score) : 0}
                    starRatedColor="blue"
                    numberOfStars={5}
                />
             
              <span onClick = {() => navigate(`user/#/${username}`)} >View Profile</span>
          </li>
        )):
        <p>nothing found</p>}
      </ul>
   </div>
</div>
  )

}


const FindJob = () => (
  <Layout>
    <SEO title="find-employee" />
    <FindEmployeeBody />
  </Layout>
)

export default FindJob